<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ products.length }} товара
      </span>
    </div>

    <section class="cart">

      <div v-if="orderLoading"> Отправка информации... <img src="img/preloading.gif"> </div>
      <div v-else-if="orderLoadingFailed"> Ошибка оформления заказа <img src="img/Неудача.gif"> </div>

      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <BaseFormText title="ФИО" placeholder="Введите Ваше имя" v-model="formData.name" :error="formError.name" />
            <BaseFormText title="Адрес доставки" placeholder="Введите адрес доставки" v-model="formData.address"
              :error="formError.address" />
            <BaseFormText title="Телефон" placeholder="Введите ваш номер телефона" v-model="formData.phone"
              :error="formError.phone" />
            <BaseFormText title="Email" placeholder="Введите адрес электронной почты" v-model="formData.email"
              :error="formError.email" />

            <BaseFormTextarea title="Комментарий к заказу" placeholder="Комментарии к заказу" v-model="formData.comment"
              :error="formError.comment" />

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.product.id">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.product.price * item.amount | numberFormat }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ products.length }}</b> товара на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

import { mapGetters } from 'vuex';

export default {
  filters: {
    numberFormat,
  },
  components: { BaseFormText, BaseFormTextarea },
  data() {

    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      orderLoading: false,
      orderLoadingFailed: false,
    }
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';

      this.orderLoading = true;
      this.orderLoadingFailed = false;

      axios
        .post(API_BASE_URL + '/api/orders', {
          ...this.formData
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          }
        })
        .then(response => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: {id: response.data.id} })
          this.orderLoading = false
        })
        .catch(error => {
          this.formError = (error.response? error.response.data.error.request:{}) || {};
          this.formErrorMessage =  error.response? error.response.data.error.message:'';
          this.orderLoadingFailed = true;
        })
        .then(() => {
          this.orderLoading = false
        })
    }
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
};

</script>
